/* Main App Styles */
.app {
  min-height: 100vh;
  padding: 2rem;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 3rem;
}

.gradient-text {
  background: linear-gradient(90deg, #fff, #a5b4fc);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-size: 3.5rem;
  margin-bottom: 1.5rem;
}

.subtitle {
  font-size: 1.3rem;
  margin-bottom: 3rem;
  opacity: 0.9;
}

.banner-image {
  width: 90%;
  max-width: 800px;
  border-radius: 20px;
  margin: 2rem auto;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.floating-animation {
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

.education-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 25px;
  margin-top: 3rem;
}

.education-card {
  position: relative;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  border-radius: 15px;
  padding: 2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  overflow: hidden;
  z-index: 1;
}

.education-card h3 {
  margin-top: 1rem;
  font-weight: 500;
}

.card-icon {
  font-size: 2.5rem;
  color: var(--card-color);
}

.hover-effect {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
  transition: 0.5s;
  z-index: -1;
}

.education-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
}

.education-card:hover .hover-effect {
  left: 100%;
}

/* Quiz Container Styles */
.quiz-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #2c3e50, #4ca1af);
  z-index: 100;
}

.back-btn {
  position: absolute;
  top: 30px;
  left: 30px;
  padding: 12px 25px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 1rem;
  cursor: pointer;
  z-index: 101;
  transition: all 0.3s ease;
}

.back-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-3px);
}

.quiz-iframe {
  width: 100%;
  height: 100%;
  border: none;
}

/* Cursor Styles */
.cursor {
  position: fixed;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: white;
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: difference;
  transform: translate(-50%, -50%);
  transition: transform 0.05s linear;
}

.cursor-trail {
  position: fixed;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.4);
  pointer-events: none;
  z-index: 9998;
  transform: translate(-50%, -50%);
  transition: transform 0.2s ease-out;
}

.glitter-particle {
  position: fixed;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  pointer-events: none;
  z-index: 9997;
  transform: translate(-50%, -50%);
  animation: glitterFade 1s forwards;
}

@keyframes glitterFade {
  0% { transform: translate(-50%, -50%) scale(1); opacity: 1; }
  100% { transform: translate(-50%, -50%) scale(0); opacity: 0; }
}

/* Responsive Styles */
@media (max-width: 768px) {
  .container {
    padding: 2rem 1rem;
  }
  
  .gradient-text {
    font-size: 2.5rem;
  }
  
  .education-grid {
    grid-template-columns: 1fr;
  }
}